body {
  color: #4a4a4a;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(135deg, #ffadc6 0%, #ffe066 100%);
  margin: 0;
  padding: 0;
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
}

.app-container {
  background: #ffffffd9;
  border-radius: 12px;
  flex-direction: column;
  align-items: center;
  max-width: 1200px;
  min-height: 100vh;
  margin: 1rem auto;
  padding: 2rem;
  display: flex;
  box-shadow: 0 8px 24px #ffa5b24d;
}

.app-title {
  color: #d94f6e;
  letter-spacing: .05em;
  user-select: none;
  margin-bottom: 2rem;
  font-size: 2.5rem;
  font-weight: 700;
}

.controls button {
  color: #fff;
  cursor: pointer;
  background-color: #ffb347;
  border: none;
  border-radius: 4px;
  padding: .5rem 1.2rem;
  font-size: 1rem;
  font-weight: 600;
  transition: background-color .2s;
}

.controls button:hover {
  background-color: #ffcf69;
}

.board {
  -webkit-overflow-scrolling: touch;
  scrollbar-width: thin;
  scrollbar-color: #ffadc6 transparent;
  flex-wrap: nowrap;
  justify-content: flex-start;
  gap: 1.5rem;
  width: 100%;
  padding-bottom: 1rem;
  display: flex;
  overflow-x: auto;
}

.board::-webkit-scrollbar-thumb {
  background-color: #ffadc6;
}

.column {
  background-color: #fff0f6;
  border-radius: 10px;
  flex-direction: column;
  flex: 0 0 272px;
  max-height: 80vh;
  padding: .75rem;
  display: flex;
  overflow-y: auto;
  box-shadow: 0 1px 6px #ffa5b266;
}

.column-title {
  color: #d94f6e;
  user-select: none;
  border-bottom: 2px solid #ffd966;
  margin-bottom: .75rem;
  padding-bottom: .3rem;
  font-size: 1.1rem;
  font-weight: 700;
}

.task {
  cursor: grab;
  user-select: none;
  color: #6b4a3b;
  background-color: #fff7e6;
  border-radius: 8px;
  justify-content: space-between;
  align-items: center;
  margin-bottom: .75rem;
  padding: .75rem 1rem;
  transition: background-color .15s;
  display: flex;
  box-shadow: 0 1px 4px #ffc44f4d;
}

.task:hover {
  background-color: #ffeabf;
}

.task.done {
  color: #8a6d2f;
  box-shadow: none;
  background-color: #fff3b0;
  text-decoration: line-through;
}

.task button {
  color: #d94f6e;
  cursor: pointer;
  user-select: none;
  background: none;
  border: none;
  padding: 0;
  font-size: 1.1rem;
}

.task button:hover {
  color: #ff5370;
}

button {
  color: #fff;
  cursor: pointer;
  user-select: none;
  background-color: #ffb347;
  border: none;
  border-radius: 6px;
  padding: .5rem 1.2rem;
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 1rem;
  font-weight: 600;
  transition: background-color .3s, box-shadow .3s;
  box-shadow: 0 2px 6px #ffb34780;
}

button:hover, button:focus {
  background-color: #ffcf69;
  outline: none;
  box-shadow: 0 3px 8px #ffcf69b3;
}

input[type="text"], select {
  color: #6b4a3b;
  outline-offset: 2px;
  user-select: text;
  box-sizing: border-box;
  background: #fff7e6;
  border: 1.5px solid #ffadc6;
  border-radius: 6px;
  outline-color: #0000;
  padding: .5rem .75rem;
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 1rem;
  font-weight: 500;
  transition: border-color .3s, box-shadow .3s;
}

input[type="text"]:focus, select:focus {
  border-color: #d94f6e;
  outline-color: #d94f6e88;
  box-shadow: 0 0 8px #d94f6e88;
}

input[type="text"], select {
  min-width: 150px;
  margin-bottom: 6px;
  margin-right: 10px;
}

select.assign-dropdown {
  width: 100%;
  margin-top: 6px;
  margin-bottom: 6px;
}

input::placeholder {
  color: #d94f6e;
  opacity: .7;
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  font-style: italic;
  font-weight: 500;
}
